import React from 'react';
import {connect} from 'react-redux';

const SideStep2 = (props) => {
  const {
    input,
    credit,
    pay,
  } = props;

  const {
    text,
  } = input;
  const {
    price,
    time,
  } = credit;
  const {
    pay_first,
    pay_per_month,
  } = pay;

  return (
    <div className='side_step_2'>
      <div className='step2_title'> 
        <div className='step2_text'>{`   ${text}`}</div>
      </div>
      <div className='step2_title'>
        <div className='step2_text'>{`  - ${price} .`}</div>
        <div className='step2_text'>{`  - ${time} .`}</div>
      </div>
      <div className='step2_title'> 
        <div className='step2_text'>{`  - ${pay_first} .`}</div>
        <div className='step2_text'>{`  - ${pay_per_month} .`}</div>
      </div>
    </div>
  )
};

const mapStateToProps = (state) => {
  const {
    input,
    credit,
    pay,
  } = state.app;
  return {
    input,
    credit,
    pay,
  }
};

export default connect(mapStateToProps)(SideStep2);



